<template>
    <div>
        <el-form
                class="detail-form-content"
                ref="ruleForm"
                :model="ruleForm"
                label-width="80px"
        >
            <el-col :span="12">
                <el-form-item v-if="flag=='yonghu'" label="" prop="yonghuming">
                    <el-input v-model="ruleForm.yonghuming"
                              placeholder="" clearable></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item v-if="flag=='yonghu'" label="" prop="xingming">
                    <el-input v-model="ruleForm.xingming"
                              placeholder="" clearable></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item v-if="flag=='yonghu'" label="" prop="mima">
                    <el-input v-model="ruleForm.mima"
                              placeholder="" clearable></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item v-if="flag=='yonghu'" label="" prop="xingbie">
                    <el-select v-model="ruleForm.xingbie" placeholder="">
                        <el-option
                                v-for="(item,index) in yonghuxingbieOptions"
                                v-bind:key="index"
                                :label="item"
                                :value="item">
                        </el-option>
                    </el-select>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item v-if="flag=='yonghu'" label="" prop="dianhua">
                    <el-input v-model="ruleForm.dianhua"
                              placeholder="" clearable></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item v-if="flag=='yonghu'" label="" prop="youxiang">
                    <el-input v-model="ruleForm.youxiang"
                              placeholder="" clearable></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item v-if="flag=='qiyexinxi'" label="" prop="qiyebianhao">
                    <el-input v-model="ruleForm.qiyebianhao"
                              placeholder="" clearable></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item v-if="flag=='qiyexinxi'" label="" prop="mima">
                    <el-input v-model="ruleForm.mima"
                              placeholder="" clearable></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item v-if="flag=='qiyexinxi'" label="" prop="qiyemingcheng">
                    <el-input v-model="ruleForm.qiyemingcheng"
                              placeholder="" clearable></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item v-if="flag=='qiyexinxi'" label="" prop="fuzeren">
                    <el-input v-model="ruleForm.fuzeren"
                              placeholder="" clearable></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item v-if="flag=='qiyexinxi'" label="" prop="lianxidianhua">
                    <el-input v-model="ruleForm.lianxidianhua"
                              placeholder="" clearable></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item v-if="flag=='qiyexinxi'" label="" prop="qiyeyouxiang">
                    <el-input v-model="ruleForm.qiyeyouxiang"
                              placeholder="" clearable></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item v-if="flag=='qiyexinxi'" label="" prop="qiyejieshao">
                    <el-input v-model="ruleForm.qiyejieshao"
                              placeholder="" clearable></el-input>
                </el-form-item>
            </el-col>
            <el-form-item v-if="flag=='users'" label="" prop="username">
                <el-input v-model="ruleForm.username"
                          placeholder=""></el-input>
            </el-form-item>
            <el-col :span="24">
                <el-form-item>
                    <el-button type="primary" @click="onUpdateHandler"> </el-button>
                </el-form-item>
            </el-col>
        </el-form>
    </div>
</template>
<script>
    // url
    import {isNumber, isIntNumer, isEmail, isMobile, isURL, checkIdCard} from "@/utils/validate";

    export default {
        data() {
            return {
                ruleForm: {},
                flag: '',
                usersFlag: false,
                yonghuxingbieOptions: [],
            };
        },
        mounted() {
            var table = this.$storage.get("sessionTable");
            this.flag = table;
            this.$http({
                url: `${this.$storage.get("sessionTable")}/session`,
                method: "get"
            }).then(({data}) => {
                if (data && data.code === 0) {
                    this.ruleForm = data.data;
                } else {
                    this.$message.error(data.msg);
                }
            });
            this.yonghuxingbieOptions = ",".split(',')
        },
        methods: {
            onUpdateHandler() {
                if ((!this.ruleForm.yonghuming) && 'yonghu' == this.flag) {
                    this.$message.error('');
                    return
                }
                if ((!this.ruleForm.xingming) && 'yonghu' == this.flag) {
                    this.$message.error('');
                    return
                }
                if ((!this.ruleForm.mima) && 'yonghu' == this.flag) {
                    this.$message.error('');
                    return
                }
                if ('yonghu' == this.flag && this.ruleForm.dianhua && (!isMobile(this.ruleForm.dianhua))) {
                    this.$message.error(``);
                    return
                }
                if ('qiyexinxi' == this.flag && this.ruleForm.lianxidianhua && (!isMobile(this.ruleForm.lianxidianhua))) {
                    this.$message.error(``);
                    return
                }
                if ('qiyexinxi' == this.flag && this.ruleForm.qiyeyouxiang && (!isEmail(this.ruleForm.qiyeyouxiang))) {
                    this.$message.error(``);
                    return
                }
                this.$http({
                    url: `${this.$storage.get("sessionTable")}/update`,
                    method: "post",
                    data: this.ruleForm
                }).then(({data}) => {
                    if (data && data.code === 0) {
                        this.$message({
                            message: "",
                            type: "success",
                            duration: 1500,
                            onClose: () => {
                            }
                        });
                    } else {
                        this.$message.error(data.msg);
                    }
                });
            }
        }
    };
</script>
<style lang="scss" scoped>
</style>
