<template>
  <div>
    <img class="bg" src="@/assets/img/bg.jpg">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="login-form">
      <h1 class="h1"></h1>
                              <el-form-item v-if="tableName=='yonghu'" label="" prop="yonghuyonghuming">
        <el-input v-model="ruleForm.yonghuming" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='yonghu'" label="" prop="yonghuxingming">
        <el-input v-model="ruleForm.xingming" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='yonghu'" label="" prop="yonghumima">
        <el-input v-model="ruleForm.mima" placeholder=""></el-input>
      </el-form-item>
                                          <el-form-item v-if="tableName=='yonghu'" label="" prop="yonghudianhua">
        <el-input v-model="ruleForm.dianhua" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='yonghu'" label="" prop="yonghuyouxiang">
        <el-input v-model="ruleForm.youxiang" placeholder=""></el-input>
      </el-form-item>
                                                <el-form-item v-if="tableName=='qiyexinxi'" label="" prop="qiyexinxiqiyebianhao">
        <el-input v-model="ruleForm.qiyebianhao" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='qiyexinxi'" label="" prop="qiyexinximima">
        <el-input v-model="ruleForm.mima" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='qiyexinxi'" label="" prop="qiyexinxiqiyemingcheng">
        <el-input v-model="ruleForm.qiyemingcheng" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='qiyexinxi'" label="" prop="qiyexinxifuzeren">
        <el-input v-model="ruleForm.fuzeren" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='qiyexinxi'" label="" prop="qiyexinxilianxidianhua">
        <el-input v-model="ruleForm.lianxidianhua" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='qiyexinxi'" label="" prop="qiyexinxiqiyeyouxiang">
        <el-input v-model="ruleForm.qiyeyouxiang" placeholder=""></el-input>
      </el-form-item>
                                                                                                                                                                                    <el-button @click="login()" class="btn-login" type="primary"></el-button>
    </el-form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      ruleForm: {
              },
      tableName:"",
      rules: {}
    };
  },
  mounted(){
    let table = this.$storage.get("loginTable");
    this.tableName = table;
  },
  methods: {
    // uuid
    getUUID () {
      return new Date().getTime();
    },
    // 
    login() {
                              if((!this.ruleForm.yonghuming) && `yonghu` == this.tableName){
        this.$message.error(``);
        return
      }
                                                            if((!this.ruleForm.xingming) && `yonghu` == this.tableName){
        this.$message.error(``);
        return
      }
                                                            if((!this.ruleForm.mima) && `yonghu` == this.tableName){
        this.$message.error(``);
        return
      }
                                                                                                                              if(`yonghu` == this.tableName && this.ruleForm.dianhua&&(!this.$validate.isMobile(this.ruleForm.dianhua))){
        this.$message.error(``);
        return
      }
                                                                                                                                                                                                                                                                                                                                    if(`qiyexinxi` == this.tableName && this.ruleForm.lianxidianhua&&(!this.$validate.isMobile(this.ruleForm.lianxidianhua))){
        this.$message.error(``);
        return
      }
                                                                  if(`qiyexinxi` == this.tableName && this.ruleForm.qiyeyouxiang&&(!this.$validate.isEmail(this.ruleForm.qiyeyouxiang))){
        this.$message.error(``);
        return
      }
                                                                                                                                                                                                                              this.$refs["ruleForm"].validate(valid => {
        if (valid) {
          this.$http({
            url: `${this.tableName}/register`,
            method: "post",
            data:this.ruleForm
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.$router.replace({ path: "/login" });
            } else {
              this.$message.error(data.msg);
            }
          });
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
</style>
